@font-face {
  font-family: "SFMono-Light";
  src: url("./SFMono-Light.otf");
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.rst__tree .rst__rowTitle {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 12px;
}

.rst__moveHandle {
  background-color: #2c3e50;
}

.treeText {
  height: 100% !important;
  line-height: 38px !important;
  font-size: 12px !important;
}

.rst__rowContents, .rst__moveHandle {
  border: none;
  border-radius: none;
  -webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
          box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
  padding-right: 0px;
}

.rst__rowContents {
  cursor: pointer;
  background-color: transparent;
}

.prop-node .rst__rowContents:hover {
  background-color: #ededed !important;
}

.diff-result {
  font-family: 'SFMono-Light' !important;
  font-size: 12px !important;
}

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  background-color: rgb(255, 255, 255) !important;
  background-image: none;
  color: rgb(0, 0, 0);
}

.new-node {
  background-color: #e6ffed !important;
}

.new-node-added {
  background-color: #f1f8ff !important;
}

.group .rst__moveHandle {
  background: #555555 url('/icons/group.svg') no-repeat center;
}

.section .rst__moveHandle {
  background: #2c3e50 url('/icons/section.svg') no-repeat center;
}

.sub-section .rst__moveHandle {
  background: #333333 url('/icons/subsection.svg') no-repeat center;
}

.property .rst__moveHandle {
  background: rgb(255, 64, 129) url('/icons/property.svg') no-repeat center;
}


